<template>
  <div>
    <section class="head">
      <div>
        <a @click="goBack" class="iconfont back"> &#xe614; </a>
      </div>
      <h1>My Orders</h1>
      <div class="chartWrap">
        <router-link :to="{ path: '/search', query: {} }">
          <span class="search iconfont"> &#xe625; </span>
        </router-link>
        <span class="bag">
          <em class="font10"> {{ indexData.shoppingCartNum }} </em>
        </span>
        <a
          href="javascript:void(0)"
          class="iconfont cart"
          @click="$router.push('cart')"
        >
          &#xe669;
        </a>
      </div>
    </section>
    <!-- @click="$router.push({path:'/orderDetail',query:{orderNo:item.orderNo})" -->
    <section class="tabNav">
      <div
        v-for="(item, i) in navList"
        :key="i"
        :class="navIndex == i ? 'active' : ''"
        @click="change(i)"
      >
        <span> {{ item.name }}</span>
      </div>
      <!-- <div class="active"> <span> All </span> </div>
        <div> <span> Unpaid </span> </div>
        <div> <span> Transporting </span> </div>
        <div> <span> Done </span> </div> -->
    </section>

    <section class="orderList">
      <ul>
        <li
          v-for="(item, index) in orderList.result"
          :key="index"
          @click="
            $router.push({
              path: '/orderDetail',
              query: { orderNo: item.orderNo }
            })
          "
        >
          <div class="topNo">
            <p>
              <span class="no"> {{ item.orderNo }} </span>
              <span class="payStyle"> {{ item.payType }} </span>
            </p>
            <p>
              <span class="payStatus"> {{ item.orderStatusStr }} </span>
            </p>
          </div>
          <div
            class="proWrap clearfix"
            v-for="(item_child, index_child) in item.prodList"
            :key="index_child"
          >
            <div class="imgWrap">
              <img :src="item_child.img0" class="img" />
            </div>
            <div class="numRight">
              <span class="iconfont"> &#xe619; </span>
              <span> {{ item_child.productQuantity }} </span>
            </div>
            <div class="rightMain">
              <p class="name">{{ item_child.productName }}</p>
              <p class="size">{{ getAttributes(item_child.attrOptList) }}</p>
            </div>
          </div>
          <div class="downRow">
            <p class="date">02/11/2019 10:33:06</p>
            <p>
              <span class="color999"> Grand Total: </span>
              <span class="total">
                {{ (item.payedAmount / 100).toFixed(2) }} AED
              </span>
            </p>
          </div>
          <div class="downBtns" v-if="item.orderStatus == 101">
            <van-button plain hairline type="primary">Cancel</van-button>
            <van-button
              plain
              hairline
              type="danger"
              @click="redirect('/payMent')"
              >Pay</van-button
            >
          </div>
          <div class="downBtns" v-if="item.orderStatus == 103">
            <span class="tips">
              Has been delivered to the STORE of xxxx Has been delivered to the
              STORE of xxxx</span
            >
            <van-button
              plain
              hairline
              type="primary"
              @click="redirect('/logistics')"
              >Logistic</van-button
            >
          </div>
          <div class="downBtns" v-if="item.orderStatus == 3">
            <van-button plain hairline type="primary">Logistic</van-button>
            <van-button plain hairline type="primary">Contact Store</van-button>
          </div>
          <div class="downBtns" v-if="item.orderStatus == 104">
            <van-button
              plain
              hairline
              type="primary"
              @click="redirect('/reviewsOrder')"
              >Review</van-button
            >
            <van-button plain hairline type="danger" @click="addCart()"
              >Buy Again</van-button
            >
          </div>
        </li>
      </ul>
    </section>
  </div>
</template>

<script>
import { getOrderTypes, getAllOrder } from "@/api/order.js";
import { mapGetters } from "vuex";
export default {
  name: "orderList",
  data() {
    return {
      navIndex: 0,
      navList: [],
      orderList: [],
      orderListData: [
        {
          no: "No:1564654",
          payStyle: "Online Pay",
          payStatusDesc: "Unpaid",
          payStatus: 1,
          proList: [
            {
              img: "static/images/beautyCarePro1.png",
              name:
                "Name of the store and  shopeone Name of the store and  shopeone",
              size: "White; L",
              num: 19
            }
          ],
          buyDate: "02/11/2019 10:33:06",
          total: 999,
          tips: ""
        },
        {
          no: "No:1564654",
          payStyle: "COD",
          payStatusDesc: "Transporting",
          payStatus: 2,
          proList: [
            {
              img: "static/images/consumerElectronicsPro2.png",
              name:
                "Name of the store and  shopeone Name of the store and  shopeone",
              size: "White; L",
              num: 19
            }
          ],
          buyDate: "02/11/2019 10:33:06",
          total: 999,
          tips: "Has been delivered to the STORE of xxxx..."
        },
        {
          no: "No:1564654",
          payStyle: "COD",
          payStatusDesc: "Transporting",
          payStatus: 3,
          proList: [
            {
              img: "static/images/dressPro1.png",
              name:
                "Name of the store and  shopeone Name of the store and  shopeone",
              size: "White; L",
              num: 19
            },
            {
              img: "static/images/dressPro2.png",
              name:
                "Name of the store and  shopeone Name of the store and  shopeone",
              size: "White; L",
              num: 19
            }
          ],
          buyDate: "02/11/2019 10:33:06",
          total: 999,
          tips: ""
        },
        {
          no: "No:1564654",
          payStyle: "Online Pay",
          payStatusDesc: "Done",
          payStatus: 4,
          proList: [
            {
              img: "static/images/dressPro3.png",
              name:
                "Name of the store and  shopeone Name of the store and  shopeone",
              size: "White; L",
              num: 19
            }
          ],
          buyDate: "02/11/2019 10:33:06",
          total: 999,
          tips: ""
        }
      ]
    };
  },
  computed: {
    ...mapGetters(["indexData"])
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      getOrderTypes().then(res => {
        this.navList = res.data.result;
        let orderStatus = res.data.result[0].orderStatus;
        getAllOrder(orderStatus, 1, 8).then(res => {
          this.orderList = res.data.result;
        });
      });
    },
    goBack() {
      this.$router.back(-1);
    },
    // 切换
    change(i) {
      this.navIndex = i;
      let orderStatus = this.navList[i].orderStatus;
      getAllOrder(orderStatus, 1, 8).then(res => {
        this.orderList = res.data.result;
      });
    },
    //添加购物车
    addCart() {},
    getAttributes(arr) {
      return arr.map(t => t.optionName).join(";");
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../common/css/orderList.scss";
</style>



